.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-bottom,
.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-left,
.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-right {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

/* Flip slide shadows end */
.swiper-creative .swiper-slide {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  transition-property: transform, opacity, height;
}

.swiper-cards {
  overflow: visible;
}

.swiper-cards .swiper-slide {
  transform-origin: center bottom;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
  --angle: -11deg;
}

html {
  scroll-behavior: smooth;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus {
  background-color: #eee;
  clip: auto !important;
  clip-path: none;
  color: #444;
  display: block;
  font-size: 1em;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

.grecaptcha-badge {
  visibility: hidden;
}

html.lenis {
  height: auto;
}

.lenis.lenis-smooth {
  scroll-behavior: auto;
}

.lenis.lenis-smooth [data-lenis-prevent] {
  overscroll-behavior: contain;
}

.lenis.lenis-stopped {
  overflow: hidden;
}

.lenis.lenis-scrolling iframe {
  pointer-events: none;
}

#header-main {
  @apply transition-all duration-700 ease-in-out shadow-transparent;
  /* Menu nav open */
}
#header-main .navigation-main {
  /* Hamburguer menu toggle */
}
#header-main .navigation-main .main-menu-toggle {
  @apply w-[50px] cursor-pointer;
}
#header-main .navigation-main .main-menu-toggle span {
  @apply block w-full h-[3px] rounded bg-white relative transition-all duration-500 ease-in-out;
}
#header-main .navigation-main .main-menu-toggle span + span {
  @apply mt-2;
}
#header-main .navigation-main .main-menu-toggle span:nth-child(1) {
  top: 0;
  transform: rotate(0deg);
}
#header-main .navigation-main .main-menu-toggle span:nth-child(2) {
  transform: scale(1);
}
#header-main .navigation-main .main-menu-toggle span:nth-child(3) {
  bottom: 0;
  transform: rotate(0);
}
#header-main .navigation-main #brister-logo {
  @apply w-[120px] md:w-[145px];
}
#header-main .navigation-main #brister-logo path {
  @apply fill-white transition-all duration-700;
}
#header-main .navigation-main .main-menu-wrapper {
  @apply absolute top-0 left-0 right-0 bottom-0 h-[var(--element-height)] invisible overflow-hidden -z-10;
}
#header-main .navigation-main .main-menu-wrapper::before {
  @apply content-[""] block absolute top-0 left-0 right-0 bottom-0 w-full h-full bg-dark-green opacity-0 transition-all duration-500 ease-in-out;
}
#header-main .navigation-main .main-menu-wrapper .main-menu-content {
  @apply bg-white w-2/3 md:w-1/4 h-full -translate-x-full transition-all duration-500 drop-shadow py-32 px-8 flex flex-col justify-between;
}
#header-main .navigation-main .main-menu-wrapper .main-menu-content #menu-main-menu {
  @apply flex flex-col gap-6;
}
#header-main .navigation-main .main-menu-wrapper .main-menu-content #menu-main-menu .menu-item a {
  @apply font-montbold text-lg text-dark-green transition-all duration-300 ease-in-out hover:text-lime-green;
}
#header-main .navigation-main.nav-open .main-menu-toggle span {
  @apply bg-dark-green;
}
#header-main .navigation-main.nav-open .main-menu-toggle span:nth-child(1) {
  top: 11px;
  transform: rotate(45deg);
}
#header-main .navigation-main.nav-open .main-menu-toggle span:nth-child(2) {
  transform: scale(0);
}
#header-main .navigation-main.nav-open .main-menu-toggle span:nth-child(3) {
  bottom: 11px;
  transform: rotate(135deg);
}
#header-main .navigation-main.nav-open #brister-logo path {
  @apply fill-dark-green;
}
#header-main .navigation-main.nav-open .main-menu-wrapper {
  @apply visible;
}
#header-main .navigation-main.nav-open .main-menu-wrapper::before {
  @apply opacity-50;
}
#header-main .navigation-main.nav-open .main-menu-wrapper .main-menu-content {
  @apply translate-x-0;
}

